// Component IconText
const IconText = ({ icon, text }) => (
  <Box sx={{ display: 'flex', alignItems: 'center', mr: 2, mb: 1 }}>
    {React.cloneElement(icon, { sx: { fontSize: 20, mr: 1, color: themeColors.primary } })}
    <Typography variant="body2">{text}</Typography>
  </Box>
);

// Component Gallery
const TourGallery = ({ images }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  
  return (
    <ImageList cols={isMobile ? 2 : 3} gap={8} variant="masonry">
      {images.map((img, index) => (
        <ImageListItem key={index}>
          <img
            src={`${img}?w=248&fit=crop&auto=format`}
            srcSet={`${img}?w=248&fit=crop&auto=format&dpr=2 2x`}
            alt={`Tour gallery ${index + 1}`}
            loading="lazy"
            style={{ borderRadius: '8px' }}
          />
        </ImageListItem>
      ))}
    </ImageList>
  );
};

// Component DetailedTourCard
const DetailedTourCard = ({ tour }) => {
  const [tabValue, setTabValue] = React.useState(0);
  const discountPercent = Math.round(
    ((tour.originalPrice - tour.discountPrice) / tour.originalPrice) * 100
  );

  return (
    <Grid item xs={12}>
      <Card sx={{ borderRadius: 2, boxShadow: 3, mb: 4 }}>
        {/* Main Image */}
        <Box sx={{ position: 'relative', height: '400px' }}>
          <img
            src={tour.mainImage}
            alt={tour.title}
            style={{
              width: '100%',
              height: '100%',
              objectFit: 'cover',
              borderTopLeftRadius: '8px',
              borderTopRightRadius: '8px'
            }}
          />
          {tour.isBestSeller && (
            <Chip
              label="Best Seller"
              size="medium"
              sx={{
                position: 'absolute',
                top: 16,
                left: 16,
                bgcolor: themeColors.bestSeller,
                color: 'white',
                fontWeight: 'bold',
                fontSize: '0.875rem'
              }}
            />
          )}
        </Box>

        <CardContent sx={{ p: 4 }}>
          {/* Tour Header */}
          <Box sx={{ display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', mb: 3 }}>
            <Box>
              <Typography variant="h3" sx={{ fontWeight: 'bold', mb: 1 }}>
                {tour.title}
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Star color="warning" />
                <Typography variant="body1" sx={{ ml: 1, mr: 3 }}>
                  {tour.rating} ({tour.reviews} reviews)
                </Typography>
                <Chip label={tour.days} size="medium" />
              </Box>
            </Box>
            
            <Box sx={{ minWidth: '200px', textAlign: 'right' }}>
              <Typography variant="h4" sx={{ color: themeColors.primary, fontWeight: 'bold' }}>
                ${tour.discountPrice}
              </Typography>
              <Typography variant="body1" sx={{ textDecoration: 'line-through' }}>
                ${tour.originalPrice}
              </Typography>
              {discountPercent > 0 && (
                <Chip
                  label={`Save ${discountPercent}%`}
                  size="small"
                  sx={{
                    bgcolor: themeColors.discount,
                    color: 'white',
                    fontWeight: 'bold',
                    mt: 1
                  }}
                />
              )}
            </Box>
          </Box>

          {/* Quick Facts */}
          <Box sx={{
            display: 'flex',
            flexWrap: 'wrap',
            bgcolor: '#f8f9fa',
            p: 2,
            borderRadius: 1,
            mb: 4
          }}>
            <IconText icon={<LocationOn />} text={tour.tags[0]} />
            <IconText icon={<CalendarToday />} text={`Departure: ${tour.departure}`} />
            <IconText icon={<Group />} text={`Group: ${tour.groupSize}`} />
            <IconText icon={<Language />} text={`Languages: ${tour.languages.join(', ')}`} />
          </Box>

          {/* Tabs */}
          <Tabs
            value={tabValue}
            onChange={(e, newValue) => setTabValue(newValue)}
            sx={{ mb: 3 }}
          >
            <Tab label="Overview" icon={<PhotoLibrary />} />
            <Tab label="Itinerary" icon={<AccessTime />} />
            <Tab label="Inclusions" icon={<Star />} />
          </Tabs>

          {/* Tab Content */}
          <Box sx={{ mb: 4 }}>
            {tabValue === 0 && (
              <Box>
                <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2 }}>
                  Tour Description
                </Typography>
                <Typography variant="body1" paragraph>
                  {tour.description}
                </Typography>

                <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, mt: 4 }}>
                  Highlights
                </Typography>
                <List dense>
                  {tour.highlights.map((item, index) => (
                    <ListItem key={index} sx={{ px: 0 }}>
                      <ListItemIcon sx={{ minWidth: 30 }}>
                        <Star fontSize="small" color="primary" />
                      </ListItemIcon>
                      <ListItemText primary={item} />
                    </ListItem>
                  ))}
                </List>

                <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, mt: 4 }}>
                  Photo Gallery
                </Typography>
                <TourGallery images={tour.images} />
              </Box>
            )}

            {tabValue === 1 && (
              <Box>
                {tour.itinerary.map((day) => (
                  <Paper key={day.day} elevation={2} sx={{ p: 3, mb: 2 }}>
                    <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                      Day {day.day}: {day.title}
                    </Typography>
                    <Typography variant="body1">
                      {day.description}
                    </Typography>
                  </Paper>
                ))}
              </Box>
            )}

            {tabValue === 2 && (
              <Grid container spacing={3}>
                <Grid item xs={12} md={6}>
                  <Paper elevation={2} sx={{ p: 3 }}>
                    <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                      What's Included
                    </Typography>
                    <List>
                      {tour.inclusions.map((item, index) => (
                        <ListItem key={index} sx={{ px: 0 }}>
                          <ListItemIcon sx={{ minWidth: 30 }}>
                            <CheckCircle color="success" />
                          </ListItemIcon>
                          <ListItemText primary={item} />
                        </ListItem>
                      ))}
                    </List>
                  </Paper>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Paper elevation={2} sx={{ p: 3 }}>
                    <Typography variant="h6" sx={{ fontWeight: 'bold', mb: 2 }}>
                      What's Excluded
                    </Typography>
                    <List>
                      {tour.exclusions.map((item, index) => (
                        <ListItem key={index} sx={{ px: 0 }}>
                          <ListItemIcon sx={{ minWidth: 30 }}>
                            <Cancel color="error" />
                          </ListItemIcon>
                          <ListItemText primary={item} />
                        </ListItem>
                      ))}
                    </List>
                  </Paper>
                </Grid>
              </Grid>
            )}
          </Box>

          {/* Tags */}
          <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, mb: 4 }}>
            {tour.tags.map((tag, index) => (
              <Chip
                key={index}
                label={tag}
                size="medium"
                sx={{
                  bgcolor: themeColors.primary,
                  color: 'white',
                  fontWeight: 'bold'
                }}
              />
            ))}
          </Box>

          {/* CTA Button */}
          <Button
            variant="contained"
            fullWidth
            size="large"
            endIcon={<FlightTakeoff />}
            sx={{
              bgcolor: themeColors.primary,
              py: 2,
              fontSize: '1.1rem',
              '&:hover': {
                bgcolor: '#00695c',
                transform: 'translateY(-2px)'
              }
            }}
          >
            Book This Tour Now
          </Button>
        </CardContent>
      </Card>
    </Grid>
  );
};

// Component chnh
const PopularTours = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 6 }}>
      <Box sx={{ textAlign: 'center', mb: 6 }}>
        <Typography variant="h2" sx={{ fontWeight: 'bold', mb: 2 }}>
          Our Premium Tours
        </Typography>
        <Typography variant="h5" sx={{ color: themeColors.textSecondary }}>
          Carefully curated travel experiences with complete transparency
        </Typography>
      </Box>

      <Grid container spacing={6}>
        {detailedTours.map((tour) => (
          <DetailedTourCard key={tour.id} tour={tour} />
        ))}
      </Grid>
    </Container>
  );
};

export default PopularTours;