const ServiceCard = ({ service, icon, color }) => {
  return (
    <Card sx={{ 
      maxWidth: 345, 
      m: 2, 
      borderRadius: 2,
      boxShadow: '0 4px 20px rgba(0,0,0,0.08)',
      transition: 'transform 0.3s',
      '&:hover': {
        transform: 'translateY(-5px)'
      }
    }}>
      <CardMedia
        component="img"
        height="140"
        image={service.image}
        alt={service.name}
      />
      <CardContent sx={{ bgcolor: themeColors.secondary }}>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
          {React.cloneElement(icon, { 
            sx: { 
              color: color, 
              mr: 1 
            } 
          })}
          <Typography 
            gutterBottom 
            variant="h6" 
            component="div"
            sx={{ 
              color: themeColors.textPrimary,
              fontWeight: 'bold'
            }}
          >
            {service.name}
          </Typography>
        </Box>
        <Typography 
          variant="body2" 
          color="text.secondary"
          sx={{ 
            color: themeColors.textSecondary,
            mb: 1
          }}
        >
          {service.description}
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
          <Typography 
            variant="body2"
            sx={{ 
              color: themeColors.accent,
              fontWeight: 'bold',
              mr: 2
            }}
          >
            {service.price}
          </Typography>
          <Typography 
            variant="body2"
            sx={{ 
              color: themeColors.textPrimary
            }}
          >
             {service.rating}
          </Typography>
        </Box>
        <Divider sx={{ my: 1 }} />
        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
          {service.tags.map((tag, index) => (
            <Chip 
              key={index} 
              label={tag} 
              size="small" 
              sx={{ 
                bgcolor: color, 
                color: 'white',
                fontSize: '0.7rem'
              }} 
            />
          ))}
        </Box>
      </CardContent>
    </Card>
  );
};

const ServiceSection = ({ title, services, icon, color }) => {
  return (
    <Box sx={{ mb: 4 }}>
      <Box sx={{ 
        display: 'flex', 
        alignItems: 'center', 
        mb: 3,
        p: 2,
        bgcolor: themeColors.primary,
        borderRadius: 1,
        boxShadow: 2
      }}>
        {React.cloneElement(icon, { 
          sx: { 
            color: 'white', 
            fontSize: '2rem', 
            mr: 2 
          } 
        })}
        <Typography 
          variant="h4" 
          sx={{ 
            color: 'white',
            fontWeight: 'bold'
          }}
        >
          {title}
        </Typography>
      </Box>
      <Grid container spacing={2} justifyContent="center">
        {services.map(service => (
          <Grid item key={service.id}>
            <ServiceCard service={service} icon={icon} color={color} />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

const TravelServices = () => {
  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography 
        variant="h3" 
        align="center" 
        gutterBottom
        sx={{ 
          color: themeColors.textPrimary,
          fontWeight: 'bold',
          mb: 4
        }}
      >
        Dch V Du Lch
      </Typography>
      
      <ServiceSection 
        title="m Thc a Phng" 
        services={services.food} 
        icon={<RestaurantIcon />} 
        color="#4CAF50" 
      />
      
      <ServiceSection 
        title="Khch Sn & Ni Lu Tr" 
        services={services.hotel} 
        icon={<HotelIcon />} 
        color="#2196F3" 
      />
      
      <ServiceSection 
        title="Phng Tin Di Chuyn" 
        services={services.transport} 
        icon={<TransportIcon />} 
        color="#FF9800" 
      />
    </Container>
  );
};

export default TravelServices;