import {
  Box,
  Typography,
  Button,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Divider,
  Grid,
} from '@mui/material';
import {
  CalendarMonth,
  Person,
  Favorite,
  Share,
  Comment,
  LocationOn,
  AccessTime,
  EmojiEmotions,
  ArrowForward,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';

const VietnamTravelBlog = () => {

    const navigate = useNavigate();
  const blogPosts = [
    {
      id: 1,
      title: 'Hi An - Nt p C Knh Gia Lng Hin i',
      excerpt: 'Khm ph nhng gi tr vn ha c sc ca ph c Hi An...',
      image: 'https://ik.imagekit.io/tvlk/blog/2022/02/dia-diem-du-lich-hoi-an-cover.jpg',
      date: '15/03/2023',
      author: 'Nguyn Vn An',
      tags: ['Di sn', 'Vn ha', 'm thc'],
      likes: 124,
      comments: 28,
      location: 'Qung Nam',
      bestTime: 'Thng 2 - Thng 4',
      experience: `
        n vi Hi An, bn s c tri nghim:
        
         Do b dc nhng con ph c ngp trn nh n lng vo bui ti
         Tham gia lp hc lm n lng truyn thng ti cc xng th cng
         Thng thc mn Cao Lu c sn - s kt hp c o gia m Qung v hng v Nht Bn
         Th hoa ng trn sng Hoi - nghi thc mang m nt vn ha tm linh
         Khm ph Cha Cu - biu tng kin trc giao thoa Vit-Nht
         Thu xe p thong th ngm nhn nhng cnh ng la bt ngn  ngoi 
        
        c bit vo ngy 14 m lch hng thng, thnh ph t chc "m ph c" 
        vi cc hot ng vn ha dn gian c sc.`,
      featured: true
    },
    {
      id: 3,
      title: 'Vng Tu - Thnh Ph Bin Nng ng',
      excerpt: 'im n l tng cho chuyn du lch ngn ngy t Si Gn...',
      image: 'https://admin.freetour.com/images/tours/7741/vung-tau-walking-tour-08.jpg',
      date: '25/05/2023',
      author: 'L Minh Khi',
      tags: ['Bin', 'Lch s', 'm hi sn'],
      likes: 178,
      comments: 32,
      location: 'B Ra - Vng Tu',
      bestTime: 'Quanh nm',
      experience: `
        Vng Tu hp dn bi:
        
         Leo ni Nh ngm tng Cha Kit Vua cao 32m
         Do Bi Sau - bi bin di 8km vi sng va phi
         Thm Bch Dinh - dinh th c thi thuc a Php
         Thng thc hi sn ti sng ti khu ph m thc Thy Vn
         Check-in Cu cng 914 - im sng o mi ni
         Ngm hong hn t nh Hi ng c - ngn hi ng c nht Vit Nam
        
        c sn khng th b qua:
        - Bnh kht C Giang
        - Sa u nnh nng
        - Gi c mai
        - Kh c ui nng`,
      featured: false
    },
    {
      id: 4,
      title: 'Ph Quc - o Ngc Gia Bin ng',
      excerpt: 'Thin ng ngh dng vi nhng bi bin nguyn s v h sinh thi c o...',
      image: 'https://th.bing.com/th/id/OIP.nEgZFf5oVekxNc9GC3drZgHaEK?rs=1&pid=ImgDetMain',
      date: '12/06/2023',
      author: 'Nguyn Tho Nguyn',
      tags: ['o ngc', 'Du lch cao cp', 'Thin nhin'],
      likes: 342,
      comments: 67,
      location: 'Kin Giang',
      bestTime: 'Thng 11 - Thng 4',
      experience: `
        Ph Quc n tim du khch bi:
        
         Tri nghim cp treo Hn Thm - tuyn cp treo qua bin di nht th gii
         Ln ngm san h  Bi Sao - bi bin p nht o
         Tham quan lng chi Hm Ninh thng thc hi sn ti sng
         Khm ph Vn Quc gia Ph Quc - khu d tr sinh quyn th gii
         Tri nghim tour m cu mc cng ng dn
         Thm c s sn xut nc mm Ph Quc ni ting
        
        Cc hot ng c bit:
        - Tm bn ti Sui Tranh
        - Ngm hong hn ti Mi Gnh Du
        - Check-in Nh th  Dinh Cu
        - Mua sm ti ch m Ph Quc`,
      featured: true
    }
  ];

  // Tm bi vit featured u tin
  const featuredPost = blogPosts.find(post => post.featured) || blogPosts[0];

  // Cc bi vit khng phi featured
  const regularPosts = blogPosts.filter(post => post.id !== featuredPost.id);

  return (
    <Box sx={{ maxWidth: 1400, mx: 'auto', py: 8, px: { xs: 2, md: 4 } }}>
      {/* Featured Post */}
      <Card sx={{ 
        mb: 6,
        borderRadius: 4,
        boxShadow: '0 15px 30px rgba(0,0,0,0.12)',
        overflow: 'hidden',
        position: 'relative',
        bgcolor: 'background.paper'
      }}>
        <Box sx={{ 
          position: 'relative',
          height: { xs: 300, md: 500 },
          overflow: 'hidden',
          '&:hover img': {
            transform: 'scale(1.05)'
          }
        }}>
          <CardMedia
            component="img"
            image={featuredPost.image}
            sx={{
              height: '100%',
              objectFit: 'cover',
              transition: 'transform 0.5s ease'
            }}
          />
          <Box sx={{
            position: 'absolute',
            bottom: 0,
            left: 0,
            right: 0,
            p: 4,
            background: 'linear-gradient(to top, rgba(0,0,0,0.7), transparent)'
          }}>
            <Chip 
              label="Bi vit ni bt"
              sx={{ 
                bgcolor: '#ff6b6b', 
                color: 'white', 
                mb: 2,
                fontSize: '0.8rem',
                fontWeight: 600
              }}
            />
            <Typography variant="h2" sx={{ 
              color: 'white',
              fontFamily: '"Playfair Display", serif',
              fontSize: { xs: '2rem', md: '3rem' },
              lineHeight: 1.2,
              textShadow: '0 2px 4px rgba(0,0,0,0.3)'
            }}>
              {featuredPost.title}
            </Typography>
          </Box>
        </Box>

        <CardContent sx={{ p: 4 }}>
          <Grid container spacing={4}>
            <Grid item xs={12} md={4}>
              <Box sx={{ 
                bgcolor: '#f8f9fa',
                p: 3,
                borderRadius: 2,
                height: '100%'
              }}>
                <Typography variant="h6" sx={{ mb: 2, color: '#008b76' }}>
                  Thng tin chuyn i
                </Typography>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <LocationOn sx={{ color: '#008b76', mr: 1 }} />
                  <Typography variant="body2">{featuredPost.location}</Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <AccessTime sx={{ color: '#008b76', mr: 1 }} />
                  <Typography variant="body2">Thi im: {featuredPost.bestTime}</Typography>
                </Box>
                <Box sx={{ mb: 3 }}>
                  {featuredPost.tags.map((tag, i) => (
                    <Chip
                      key={i}
                      label={tag}
                      size="small"
                      sx={{ 
                        mr: 1,
                        mb: 1,
                        bgcolor: '#e9f5f3',
                        color: '#008b76',
                        fontWeight: 500
                      }}
                       
                    />
                   
                  ))}
                </Box>
                <Divider sx={{ my: 2 }} />
                <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                  <Box sx={{ display: 'flex', alignItems: 'center' }}>
                    <Person sx={{ color: 'text.secondary', mr: 1 }} />
                    <Typography variant="caption">{featuredPost.author}</Typography>
                  </Box>
                  <Typography variant="caption">{featuredPost.date}</Typography>
                </Box>
              </Box>
            </Grid>

            <Grid item xs={12} md={8}>
              <Typography variant="h5" sx={{ 
                mb: 3,
                fontFamily: '"Playfair Display", serif',
                color: '#2d3436'
              }}>
                Tri nghim ng nh
              </Typography>
              
              {featuredPost.experience.split('\n').map((line, i) => (
                <Typography 
                  key={i} 
                  paragraph 
                  sx={{ 
                    color: 'text.secondary',
                    lineHeight: 1.8,
                    '&:before': {
                      content: line.trim() ? '" "' : '""',
                      color: '#008b76',
                      fontWeight: 'bold',
                      display: i > 0 ? 'inline' : 'none'
                    }
                  }}
                >
                  {line.trim()}
                </Typography>
              ))}

              <Box sx={{ 
                mt: 4,
                display: 'flex',
                gap: 2,
                borderTop: '1px solid #eee',
                pt: 3
              }}>
                <Button 
                  variant="contained" 
                  startIcon={<Favorite />}
                  sx={{
                    bgcolor: '#ff6b6b',
                    '&:hover': { bgcolor: '#ff5252' },
                    textTransform: 'none'
                  }}
                >
                  {featuredPost.likes} Lt thch
                </Button>
                <Button
                  variant="outlined"
                  startIcon={<Comment />}
                  sx={{
                    borderColor: '#008b76',
                    color: '#008b76',
                    textTransform: 'none'
                  }}
                >
                  {featuredPost.comments} Bnh lun
                </Button>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Cc bi vit khc */}
      <Grid container spacing={4}>
        {regularPosts.map((post) => (
          <Grid item xs={12} md={6} key={post.id}>
            <Card sx={{ 
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              borderRadius: 3,
              boxShadow: '0 5px 15px rgba(0,0,0,0.08)',
              transition: 'transform 0.3s',
              '&:hover': {
                transform: 'translateY(-5px)'
              }
              
            }}
            >
              <CardMedia
                component="img"
                height="240"
                image={post.image}
                sx={{ 
                  objectFit: 'cover',
                  borderTopLeftRadius: 12,
                  borderTopRightRadius: 12 
                }}
              />
              <CardContent sx={{ flexGrow: 1, p: 3 }}>
                <Typography variant="h6" sx={{ 
                  mb: 2,
                  fontFamily: '"Playfair Display", serif',
                  lineHeight: 1.3
                }}>
                  {post.title}
                </Typography>
                
                <Typography paragraph sx={{ 
                  color: 'text.secondary',
                  fontSize: '0.9rem',
                  lineHeight: 1.6,
                  WebkitLineClamp: 3,
                  display: '-webkit-box',
                  WebkitBoxOrient: 'vertical',
                  overflow: 'hidden'
                }}>
                  {post.excerpt}
                </Typography>
                
                <Button
                  fullWidth
                  endIcon={<ArrowForward />}
                  sx={{
                    color: '#008b76',
                    fontWeight: 500,
                    mt: 'auto'
                  }}
                   onClick={() => navigate(`/detail/${post.id}`, { state: { post } })}
                >
                  c tip
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default VietnamTravelBlog;