import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Button,
  Container,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  Avatar,
  Stack,
  useTheme,
  Fade,
  Slide,
  Grow,
  Zoom,
  useScrollTrigger,
  TextField,
  InputAdornment,
  Tabs,
  Tab,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  IconButton
} from '@mui/material';
import {
  Flight,
  Hotel,
  Restaurant,
  Attractions,
  Map,
  LocalOffer,
  Star,
  CheckCircle,
  Groups,
  Public,
  Phone,
  Email,
  Facebook,
  Instagram,
  Twitter,
  Pinterest,
  Search,
  ExpandMore,
  DateRange,
  Person,
  Place,
  Favorite,
  Share,
  Directions,
  LocalAirport,
  BeachAccess,
  Terrain,
  Nightlife,
  Museum
} from '@mui/icons-material';
import Header from './Header';
import Footer from './Footer';

const primaryColor = '#008b76';
const secondaryColor = '#ff7e67';

const TravelIntroduce = () => {
  const theme = useTheme();
  const [value, setValue] = useState(0);
  const [expanded, setExpanded] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  
  // Hiu ng scroll
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100
  });

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  // D liu cc dch v
  const services = [
    {
      icon: <Flight fontSize="large" sx={{ color: primaryColor }} />,
      title: "t v my bay",
      description: "H thng so snh gi t 100+ hng hng khng, tm chuyn bay gi r nht"
    },
    {
      icon: <Hotel fontSize="large" sx={{ color: primaryColor }} />,
      title: "Khch sn",
      description: "Hn 2 triu khch sn trn ton cu vi mc gi tt nht v nhiu u i"
    },
    {
      icon: <Restaurant fontSize="large" sx={{ color: primaryColor }} />,
      title: "m thc",
      description: "Gi  nh hng ngon, a im n ung a phng vi nh gi thc t"
    },
    {
      icon: <Attractions fontSize="large" sx={{ color: primaryColor }} />,
      title: "Tri nghim",
      description: "Tour du lch v hot ng c o do ngi bn a thit k"
    }
  ];

  // im n ph bin
  const popularDestinations = [
    {
      name: " Nng",
      image: "https://vietfuntravel.com/image/data/Blog/Dalat/valley-of-love-dalat-Dalat.jpeg",
      description: "Thnh ph ng sng nht Vit Nam vi nhng bi bin tuyt p",
      type: "Bin",
      rating: 4.8,
      price: "VND 2.500.000"
    },
    {
      name: "Ph Quc",
      image: "https://th.bing.com/th/id/R.16e37ab399eb6ff6ea9863f1048954b1?rik=XrRqYQG1jUvoEQ&pid=ImgRaw&r=0",
      description: "o ngc vi nhng resort sang trng v thin nhin hoang s",
      type: "o",
      rating: 4.9,
      price: "VND 3.200.000"
    },
    {
      name: "H Ni",
      image: "https://static.vinwonders.com/2022/06/dia-diem-du-lich-ha-noi-thumb.jpg",
      description: "Th  nghn nm vn hin vi m thc phong ph v di tch lch s",
      type: "Vn ha",
      rating: 4.7,
      price: "VND 1.800.000"
    },
    {
      name: "Nha Trang",
      image: "https://bomanhatrang.com/wp-content/uploads/2023/04/bai-bien-nha-trang-1.jpg",
      description: "Thnh ph bin xinh p vi nhng hn o v h sinh thi bin a dng",
      type: "Bin",
      rating: 4.6,
      price: "VND 2.700.000"
    }
  ];

  // Tnh nng ni bt
  const features = [
    {
      title: "Gi  thng minh",
      description: "H thng AI phn tch s thch v  xut lch trnh ph hp",
      icon: <CheckCircle sx={{ fontSize: 40, mb: 2, color: 'white' }} />
    },
    {
      title: "Gi tt nht",
      description: "m bo gi r nht vi chnh sch hon tin chnh lch",
      icon: <LocalOffer sx={{ fontSize: 40, mb: 2, color: 'white' }} />
    },
    {
      title: "nh gi thc",
      description: "Nhn xt t khch du lch thc t, khng review o",
      icon: <Star sx={{ fontSize: 40, mb: 2, color: 'white' }} />
    },
    {
      title: "H tr 24/7",
      description: "i ng chm sc khch hng lun sn sng h tr mi lc",
      icon: <Groups sx={{ fontSize: 40, mb: 2, color: 'white' }} />
    }
  ];

  // nh gi t khch hng
  const testimonials = [
    {
      name: "Nguyn Th Hng",
      avatar: "https://randomuser.me/api/portraits/women/65.jpg",
      comment: "Ti  tit kim c 30% chi ph chuyn i nh TravelSmart. Dch v tuyt vi!",
      rating: 5,
      date: "15/03/2023"
    },
    {
      name: "Trn Vn Minh",
      avatar: "https://randomuser.me/api/portraits/men/32.jpg",
      comment: "Lch trnh thng minh gip ti khm ph c nhiu im n th v m khng b l g.",
      rating: 4,
      date: "22/04/2023"
    },
    {
      name: "L Th Lan",
      avatar: "https://randomuser.me/api/portraits/women/44.jpg",
      comment: "ng dng d s dng, thng tin chnh xc. Ti s tip tc s dng cho cc chuyn i sau.",
      rating: 5,
      date: "05/05/2023"
    }
  ];

  // Loi hnh du lch
  const travelTypes = [
    { icon: <BeachAccess sx={{ mr: 1 }} />, label: "Bin o" },
    { icon: <Terrain sx={{ mr: 1 }} />, label: "Ni rng" },
    { icon: <Museum sx={{ mr: 1 }} />, label: "Vn ha" },
    { icon: <Nightlife sx={{ mr: 1 }} />, label: "Gii tr" },
    { icon: <LocalAirport sx={{ mr: 1 }} />, label: "Nc ngoi" }
  ];

  // Cu hi thng gp
  const faqs = [
    {
      question: "TravelSmart c ph dch v khng?",
      answer: "TravelSmart hon ton min ph khi s dng cc dch v tm kim v so snh gi. Chng ti ch thu ph khi bn thc hin t ch qua h thng ca chng ti."
    },
    {
      question: "Lm sao  m bo gi tt nht?",
      answer: "H thng ca chng ti lin tc cp nht v so snh gi t cc nh cung cp. Chng ti cam kt hon tin chnh lch nu bn tm thy gi r hn vi cng iu kin."
    },
    {
      question: "Ti c th hy t phng khng?",
      answer: "Ty thuc vo chnh sch ca tng nh cung cp. Bn c th kim tra iu kin hy phng trc khi t v thc hin hy phng trc tip trn ng dng."
    },
    {
      question: "TravelSmart c h tr khch hng 24/7 khng?",
      answer: "i ng h tr khch hng ca chng ti lun sn sng 24/7 qua nhiu knh: in thoi, email, chat trc tuyn  gii quyt mi thc mc ca bn."
    }
  ];

  return (
    <Box sx={{ backgroundColor: theme.palette.background.default }}>
      <Header />
      
      {/* Phn Hero Banner vi hiu ng parallax */}
      <Box sx={{
        backgroundImage: 'linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url(https://images.unsplash.com/photo-1506929562872-bb421503ef21)',
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed',
        backgroundPosition: 'center',
        color: 'white',
        py: 15,
        textAlign: 'center',
        position: 'relative',
        overflow: 'hidden'
      }}>
        <Container maxWidth="md">
          <Fade in={isVisible} timeout={1000}>
            <Box>
              <Typography variant="h2" component="h1" sx={{ 
                mb: 3, 
                fontWeight: 'bold',
                textShadow: '2px 2px 4px rgba(0,0,0,0.5)',
                animation: 'fadeInDown 1s ease'
              }}>
                TravelSmart - Du Lch Thng Minh
              </Typography>
              <Typography variant="h5" sx={{ 
                mb: 5,
                textShadow: '1px 1px 2px rgba(0,0,0,0.5)',
                animation: 'fadeInUp 1s ease'
              }}>
                Cng ngh AI gip bn ln k hoch du lch hon ho vi chi ph ti u
              </Typography>
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} justifyContent="center">
                <Button 
                  variant="contained" 
                  size="large" 
                  sx={{ 
                    backgroundColor: primaryColor,
                    '&:hover': { backgroundColor: '#00695f' },
                    animation: 'pulse 2s infinite'
                  }}
                >
                  Bt u ngay
                </Button>
                <Button 
                  variant="outlined" 
                  size="large" 
                  color="inherit"
                  sx={{
                    '&:hover': { backgroundColor: 'rgba(255,255,255,0.1)' }
                  }}
                >
                  Tm hiu thm
                </Button>
              </Stack>
            </Box>
          </Fade>
        </Container>
      </Box>

      {/* Thanh tm kim ni */}
      <Slide direction="down" in={trigger} mountOnEnter unmountOnExit>
        <Paper elevation={3} sx={{
          position: 'sticky',
          top: 0,
          zIndex: 1100,
          p: 2,
          mx: 'auto',
          maxWidth: 'lg',
          backgroundColor: 'white',
          borderRadius: 2,
          boxShadow: `0 4px 20px 0 rgba(0,139,118,0.2)`,
          transform: 'translateY(-30px)',
          width: '90%'
        }}>
          <Tabs
            value={value}
            onChange={handleChange}
            variant="scrollable"
            scrollButtons="auto"
            aria-label="scrollable auto tabs example"
            sx={{
              '& .MuiTabs-indicator': {
                backgroundColor: primaryColor
              }
            }}
          >
            <Tab label="Khch sn" icon={<Hotel />} iconPosition="start" />
            <Tab label="V my bay" icon={<Flight />} iconPosition="start" />
            <Tab label="Tour du lch" icon={<Directions />} iconPosition="start" />
            <Tab label="Thu xe" icon={<Directions />} iconPosition="start" />
            <Tab label="m thc" icon={<Restaurant />} iconPosition="start" />
          </Tabs>
          
          <Grid container spacing={2} sx={{ mt: 1 }}>
            <Grid item xs={12} md={4}>
              <TextField
                fullWidth
                placeholder="im n"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Place color="action" />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <TextField
                fullWidth
                type="date"
                InputLabelProps={{ shrink: true }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <DateRange color="action" />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <TextField
                fullWidth
                placeholder="S khch"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Person color="action" />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} md={2}>
              <Button 
                fullWidth 
                variant="contained" 
                size="large"
                sx={{
                  backgroundColor: primaryColor,
                  '&:hover': { backgroundColor: '#00695f' },
                  height: '56px'
                }}
                startIcon={<Search />}
              >
                Tm kim
              </Button>
            </Grid>
          </Grid>
        </Paper>
      </Slide>

      {/* Gii thiu ngn vi hiu ng */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Grid container spacing={6} alignItems="center">
          <Grid item xs={12} md={6}>
            <Slide direction="right" in={isVisible} mountOnEnter>
              <Box>
                <Chip 
                  label="Gii thiu" 
                  color="primary" 
                  sx={{ 
                    backgroundColor: primaryColor,
                    color: 'white',
                    mb: 2,
                    px: 1,
                    fontSize: '0.8rem'
                  }} 
                />
                <Typography variant="h4" sx={{ mb: 3, fontWeight: 'bold', color: primaryColor }}>
                  Ti sao chn TravelSmart?
                </Typography>
                <Typography paragraph sx={{ mb: 3 }}>
                  TravelSmart l nn tng du lch thng minh ng dng tr tu nhn to  mang n cho bn nhng tri nghim du lch hon ho nht.
                </Typography>
                <Typography paragraph sx={{ mb: 3 }}>
                  Chng ti kt hp cng ngh hin i vi hiu bit su sc v du lch  to ra cc gii php t ch thng minh, gi  lch trnh ti u v tit kim chi ph cho bn.
                </Typography>
                <Stack direction="row" spacing={2}>
                  <Button 
                    variant="contained" 
                    size="large"
                    sx={{
                      backgroundColor: primaryColor,
                      '&:hover': { backgroundColor: '#00695f' }
                    }}
                  >
                    Khm ph ngay
                  </Button>
                  <Button 
                    variant="outlined" 
                    size="large"
                    sx={{
                      color: primaryColor,
                      borderColor: primaryColor,
                      '&:hover': { borderColor: '#00695f' }
                    }}
                  >
                    Xem video
                  </Button>
                </Stack>
              </Box>
            </Slide>
          </Grid>
          <Grid item xs={12} md={6}>
            <Zoom in={isVisible} timeout={1000}>
              <Box sx={{ 
                position: 'relative',
                borderRadius: '16px',
                overflow: 'hidden',
                boxShadow: `0 10px 30px -5px rgba(0,139,118,0.3)`,
                '&:hover img': {
                  transform: 'scale(1.05)'
                }
              }}>
                <img 
                  src="https://images.unsplash.com/photo-1552733407-5d5c46c3bb3b" 
                  alt="Travel Smart" 
                  style={{ 
                    width: '100%', 
                    transition: 'transform 0.5s ease',
                    display: 'block'
                  }} 
                />
                <Box sx={{
                  position: 'absolute',
                  bottom: 0,
                  left: 0,
                  right: 0,
                  background: 'linear-gradient(to top, rgba(0,139,118,0.7), transparent)',
                  color: 'white',
                  p: 3,
                  textAlign: 'center'
                }}>
                  <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                    Hn 1 triu khch hng hi lng
                  </Typography>
                </Box>
              </Box>
            </Zoom>
          </Grid>
        </Grid>
      </Container>

      {/* Dch v vi hiu ng hover */}
      <Box sx={{ 
        backgroundColor: '#f9f9f9', 
        py: 8,
        position: 'relative',
        '&:before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          height: '10px',
          background: `linear-gradient(to right, ${primaryColor}, ${secondaryColor})`
        }
      }}>
        <Container maxWidth="lg">
          <Fade in={isVisible} timeout={1000}>
            <Box sx={{ textAlign: 'center', mb: 6 }}>
              <Typography variant="h4" sx={{ 
                mb: 2, 
                fontWeight: 'bold',
                color: primaryColor,
                position: 'relative',
                display: 'inline-block',
                '&:after': {
                  content: '""',
                  position: 'absolute',
                  bottom: '-10px',
                  left: '50%',
                  transform: 'translateX(-50%)',
                  width: '80px',
                  height: '3px',
                  backgroundColor: secondaryColor,
                  borderRadius: '3px'
                }
              }}>
                Dch v ca chng ti
              </Typography>
              <Typography variant="subtitle1" color="text.secondary">
                Tt c nhng g bn cn cho mt chuyn i hon ho
              </Typography>
            </Box>
          </Fade>
          <Grid container spacing={4}>
            {services.map((service, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <Grow in={isVisible} timeout={index * 500}>
                  <Card sx={{ 
                    height: '100%', 
                    display: 'flex', 
                    flexDirection: 'column',
                    alignItems: 'center',
                    textAlign: 'center',
                    p: 3,
                    transition: 'all 0.3s ease',
                    borderRadius: '12px',
                    border: '1px solid rgba(0,139,118,0.1)',
                    '&:hover': {
                      transform: 'translateY(-10px)',
                      boxShadow: `0 15px 30px -5px rgba(0,139,118,0.2)`,
                      borderColor: primaryColor
                    }
                  }}>
                    <Box sx={{ 
                      mb: 2,
                      width: '80px',
                      height: '80px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      borderRadius: '50%',
                      backgroundColor: 'rgba(0,139,118,0.1)'
                    }}>
                      {service.icon}
                    </Box>
                    <Typography variant="h6" sx={{ 
                      mb: 2, 
                      fontWeight: 'bold',
                      color: primaryColor
                    }}>
                      {service.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {service.description}
                    </Typography>
                    <Button 
                      variant="text" 
                      size="small" 
                      sx={{ 
                        mt: 2,
                        color: primaryColor,
                        '&:hover': {
                          backgroundColor: 'rgba(0,139,118,0.1)'
                        }
                      }}
                    >
                      Xem chi tit
                    </Button>
                  </Card>
                </Grow>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Loi hnh du lch */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" align="center" sx={{ 
          mb: 6, 
          fontWeight: 'bold',
          color: primaryColor,
          position: 'relative',
          '&:after': {
            content: '""',
            position: 'absolute',
            bottom: '-10px',
            left: '50%',
            transform: 'translateX(-50%)',
            width: '80px',
            height: '3px',
            backgroundColor: secondaryColor,
            borderRadius: '3px'
          }
        }}>
          Loi hnh du lch
        </Typography>
        <Grid container spacing={2} justifyContent="center">
          {travelTypes.map((type, index) => (
            <Grid item key={index}>
              <Button
                variant="outlined"
                startIcon={type.icon}
                sx={{
                  borderRadius: '20px',
                  borderColor: primaryColor,
                  color: primaryColor,
                  '&:hover': {
                    backgroundColor: 'rgba(0,139,118,0.1)',
                    borderColor: '#00695f'
                  }
                }}
              >
                {type.label}
              </Button>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* im n ph bin vi hiu ng */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" align="center" sx={{ 
          mb: 6, 
          fontWeight: 'bold',
          color: primaryColor
        }}>
          im n ph bin
        </Typography>
        <Grid container spacing={4}>
          {popularDestinations.map((destination, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <Slide direction="up" in={isVisible} timeout={index * 300}>
                <Card sx={{ 
                  position: 'relative',
                  height: 350,
                  borderRadius: 3,
                  overflow: 'hidden',
                  boxShadow: '0 5px 15px rgba(0,0,0,0.1)',
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-5px)',
                    boxShadow: `0 10px 25px rgba(0,139,118,0.2)`
                  },
                  '&:hover img': {
                    transform: 'scale(1.1)'
                  }
                }}>
                  <CardMedia
                    component="img"
                    height="100%"
                    image={destination.image}
                    sx={{ 
                      transition: 'transform 0.5s ease',
                      filter: 'brightness(0.8)'
                    }}
                  />
                  <Box sx={{
                    position: 'absolute',
                    bottom: 0,
                    left: 0,
                    right: 0,
                    p: 3,
                    color: 'white',
                    background: 'linear-gradient(to top, rgba(0,0,0,0.8), transparent)'
                  }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                      <Chip 
                        label={destination.type} 
                        size="small" 
                        sx={{ 
                          backgroundColor: primaryColor,
                          color: 'white'
                        }} 
                      />
                      <Box sx={{ display: 'flex', alignItems: 'center' }}>
                        <Star sx={{ color: '#ffc107', fontSize: '18px', mr: 0.5 }} />
                        <Typography variant="body2">{destination.rating}</Typography>
                      </Box>
                    </Box>
                    <Typography variant="h6" sx={{ fontWeight: 'bold' }}>
                      {destination.name}
                    </Typography>
                    <Typography variant="body2" sx={{ mb: 1 }}>
                      {destination.description}
                    </Typography>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                      <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
                        {destination.price}
                      </Typography>
                      <Button 
                        variant="contained" 
                        size="small"
                        sx={{
                          backgroundColor: primaryColor,
                          '&:hover': { backgroundColor: '#00695f' },
                          minWidth: 'unset',
                          p: '4px 8px',
                          borderRadius: '12px'
                        }}
                      >
                        Xem
                      </Button>
                    </Box>
                  </Box>
                  <Box sx={{ 
                    position: 'absolute', 
                    top: 16, 
                    right: 16,
                    display: 'flex'
                  }}>
                    <IconButton sx={{ color: 'white' }}>
                      <Favorite />
                    </IconButton>
                    <IconButton sx={{ color: 'white' }}>
                      <Share />
                    </IconButton>
                  </Box>
                </Card>
              </Slide>
            </Grid>
          ))}
        </Grid>
        <Box sx={{ textAlign: 'center', mt: 6 }}>
          <Button 
            variant="outlined" 
            sx={{ 
              color: primaryColor,
              borderColor: primaryColor,
              '&:hover': { borderColor: '#00695f' },
              px: 4,
              py: 1.5
            }}
          >
            Xem tt c im n
          </Button>
        </Box>
      </Container>

      {/* Tnh nng ni bt */}
      <Box sx={{ 
        background: `linear-gradient(135deg, ${primaryColor}, ${secondaryColor})`, 
        py: 8, 
        color: 'white',
        position: 'relative',
        overflow: 'hidden',
        '&:before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundImage: 'radial-gradient(circle at 30% 50%, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0) 50%)'
        }
      }}>
        <Container maxWidth="lg">
          <Typography variant="h4" align="center" sx={{ 
            mb: 6, 
            fontWeight: 'bold',
            position: 'relative'
          }}>
            Tnh nng ni bt
          </Typography>
          <Grid container spacing={4}>
            {features.map((feature, index) => (
              <Grid item xs={12} md={3} key={index}>
                <Fade in={isVisible} timeout={index * 500}>
                  <Paper sx={{ 
                    p: 3, 
                    height: '100%',
                    backgroundColor: 'rgba(255,255,255,0.1)',
                    backdropFilter: 'blur(10px)',
                    borderRadius: 3,
                    border: '1px solid rgba(255,255,255,0.2)',
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      transform: 'translateY(-5px)',
                      boxShadow: '0 10px 20px rgba(0,0,0,0.2)'
                    }
                  }}>
                    {feature.icon}
                    <Typography variant="h6" sx={{ mb: 2, fontWeight: 'bold' }}>
                      {feature.title}
                    </Typography>
                    <Typography variant="body2">
                      {feature.description}
                    </Typography>
                  </Paper>
                </Fade>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Cch hot ng */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" align="center" sx={{ 
          mb: 6, 
          fontWeight: 'bold',
          color: primaryColor
        }}>
          TravelSmart hot ng nh th no?
        </Typography>
        <Grid container spacing={6}>
          {[1, 2, 3].map((step, index) => (
            <Grid item xs={12} md={4} key={index}>
              <Zoom in={isVisible} timeout={index * 500}>
                <Box sx={{ 
                  textAlign: 'center', 
                  p: 3,
                  position: 'relative',
                  '&:after': index !== 2 ? {
                    content: '""',
                    position: 'absolute',
                    top: '80px',
                    right: '-50px',
                    width: '100px',
                    height: '20px',
                    backgroundImage: 'url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyNCAyNCI+PHBhdGggZmlsbD0iIzAwOGI3NiIgZD0iTTgsNUwxNSwxMkw4LDE5VjVaIi8+PC9zdmc+")',
                    backgroundRepeat: 'no-repeat',
                    backgroundPosition: 'center',
                    display: { xs: 'none', md: 'block' }
                  } : null
                }}>
                  <Avatar sx={{ 
                    bgcolor: primaryColor, 
                    width: 80, 
                    height: 80, 
                    mb: 3,
                    mx: 'auto',
                    fontSize: '2rem',
                    fontWeight: 'bold'
                  }}>
                    {step}
                  </Avatar>
                  <Typography variant="h6" sx={{ mb: 2, fontWeight: 'bold' }}>
                    {step === 1 ? "Nhp thng tin" : step === 2 ? "Nhn lch trnh" : "t v & Tn hng"}
                  </Typography>
                  <Typography>
                    {step === 1 
                      ? "Cho chng ti bit im n, ngy i, s thch v ngn sch ca bn" 
                      : step === 2 
                        ? "H thng AI s to lch trnh ti u vi cc im n, khch sn v hot ng ph hp" 
                        : "t v, khch sn v cc dch v ngay trn ng dng vi gi tt nht"}
                  </Typography>
                </Box>
              </Zoom>
            </Grid>
          ))}
        </Grid>
      </Container>

      {/* Thng k n tng */}
      <Box sx={{ 
        backgroundColor: '#f9f9f9',
        py: 8,
        backgroundImage: 'linear-gradient(rgba(255,255,255,0.9), rgba(255,255,255,0.9)), url(https://images.unsplash.com/photo-1499678329028-101435549a4e)',
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed'
      }}>
        <Container maxWidth="lg">
          <Grid container spacing={4} justifyContent="center">
            {[
              { value: '1M+', label: 'Khch hng' },
              { value: '50+', label: 'Quc gia' },
              { value: '10K+', label: 'i tc' },
              { value: '98%', label: 'Hi lng' }
            ].map((stat, index) => (
              <Grid item xs={6} sm={3} key={index}>
                <Fade in={isVisible} timeout={index * 500}>
                  <Box sx={{ 
                    textAlign: 'center',
                    p: 3,
                    backgroundColor: 'rgba(255,255,255,0.8)',
                    borderRadius: 3,
                    boxShadow: '0 5px 15px rgba(0,0,0,0.05)',
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      transform: 'translateY(-5px)',
                      boxShadow: `0 10px 25px rgba(0,139,118,0.1)`
                    }
                  }}>
                    <Typography variant="h3" sx={{ 
                      fontWeight: 'bold',
                      color: primaryColor,
                      mb: 1
                    }}>
                      {stat.value}
                    </Typography>
                    <Typography variant="h6" color="text.secondary">
                      {stat.label}
                    </Typography>
                  </Box>
                </Fade>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* nh gi khch hng */}
      <Box sx={{ backgroundColor: 'white', py: 8 }}>
        <Container maxWidth="lg">
          <Typography variant="h4" align="center" sx={{ 
            mb: 6, 
            fontWeight: 'bold',
            color: primaryColor
          }}>
            Khch hng ni g v chng ti
          </Typography>
          <Grid container spacing={4}>
            {testimonials.map((testimonial, index) => (
              <Grid item xs={12} md={4} key={index}>
                <Grow in={isVisible} timeout={index * 500}>
                  <Card sx={{ 
                    height: '100%', 
                    p: 3,
                    position: 'relative',
                    border: '1px solid rgba(0,139,118,0.1)',
                    boxShadow: 'none',
                    transition: 'all 0.3s ease',
                    '&:hover': {
                      boxShadow: `0 10px 30px rgba(0,139,118,0.1)`
                    },
                    '&:before': {
                      content: '""',
                      position: 'absolute',
                      top: 0,
                      left: 0,
                      right: 0,
                      height: '4px',
                      background: `linear-gradient(to right, ${primaryColor}, ${secondaryColor})`
                    }
                  }}>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                     
                                            
                        <Box sx={{ flexGrow: 1 }}>
                        <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                          {testimonial.name}
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                          {testimonial.date}
                        </Typography>
                      </Box>
                    </Box>
                    
                    {/* Rating Stars */}
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      {[...Array(5)].map((_, i) => (
                        <Star
                          key={i}
                          sx={{
                            color: i < testimonial.rating ? secondaryColor : '#e0e0e0',
                            fontSize: '20px',
                            mr: 0.5
                          }}
                        />
                      ))}
                    </Box>

                    <Typography variant="body1" sx={{ mb: 2, fontStyle: 'italic' }}>
                      "{testimonial.comment}"
                    </Typography>

                    <Box sx={{ 
                      display: 'flex', 
                      alignItems: 'center', 
                      justifyContent: 'space-between',
                      mt: 'auto'
                    }}>
                      <Button
                        variant="outlined"
                        size="small"
                        sx={{
                          borderColor: primaryColor,
                          color: primaryColor,
                          '&:hover': { borderColor: '#00695f' }
                        }}
                      >
                        Xem chi tit
                      </Button>
                      <Box sx={{ display: 'flex' }}>
                        <IconButton sx={{ color: primaryColor }}>
                          <Favorite />
                        </IconButton>
                        <IconButton sx={{ color: primaryColor }}>
                          <Share />
                        </IconButton>
                      </Box>
                    </Box>
                  </Card>
                </Grow>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Cu hi thng gp */}
      <Container maxWidth="lg" sx={{ py: 8 }}>
        <Typography variant="h4" align="center" sx={{ 
          mb: 6, 
          fontWeight: 'bold',
          color: primaryColor
        }}>
          Cu hi thng gp
        </Typography>
        <Box sx={{ maxWidth: 800, mx: 'auto' }}>
          {faqs.map((faq, index) => (
            <Accordion 
              key={index} 
              elevation={2} 
              sx={{ 
                mb: 2, 
                borderRadius: '8px!important',
                '&:before': { display: 'none' }
              }}
            >
              <AccordionSummary
                expandIcon={<ExpandMore sx={{ color: primaryColor }} />}
                sx={{
                  backgroundColor: '#f8fafc',
                  borderRadius: '8px',
                  '&:hover': { backgroundColor: '#f1f5f9' }
                }}
              >
                <Typography variant="h6" sx={{ fontWeight: 600 }}>
                  {faq.question}
                </Typography>
              </AccordionSummary>
              <AccordionDetails sx={{ borderTop: `1px solid ${primaryColor}20` }}>
                <Typography>
                  {faq.answer}
                </Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Container>

      {/* Lin h */}
     

      <Footer />
    </Box>
  );
};

export default TravelIntroduce;